import React, { Component } from 'react';
import './App.css';
import { Route, Link } from 'react-router-dom';
import ToChange from './text/toChange.js';
class Welcome extends Component {
  constructor(props) {
    super(props);
    this.state = {
    };
    this.toPath=this.toPath.bind(this);
  }
  toPath(){
    this.props.history.push('/home2/ToChange')
  }
  componentDidMount() {
    console.log(this.props.history)
    
  }
  render() {
    return (
      <div>
        <h1 onClick={this.toPath}>Hello Welcome, {this.props.name}</h1>
        {this.props.children}
        <Link to={{ pathname: "/Welcome/ToChange" }} >内置路由</Link>
        <div>
          <Route path="/Welcome/ToChange" exact component={ToChange} />
        </div>
      </div>
    );
  }
}
export default Welcome;